.Friend{
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 16px 0;
    position: relative;
    &+.Friend::after{
            content: "";
            position: absolute;
            background: rgba(33, 33, 33, .08);
            width: calc(100% - 72px);
            height: 1px;
            left: 72px;
            top: 0;
    }
    .FriendInfo{
        display: flex;
        flex-direction: column;
        .FriendRole{
            color: rgba(0, 0, 0, .6);
        }
    }
}