.AppHead{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1000000;
        background-color: var(--color-White);
        padding: 14px 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        box-shadow: 0px 0px 1px rgba(0, 0, 0, .039), 0px 0.5px 1.5px rgba(0, 0, 0, 0.19);
        .title{
            width: 216px;
        }
    }